      "If the spam complaint was accidental, or you would like to begin receiving emails again, you can resubscribe to emails by clicking the button on the previous screen.":
        "در صورتی که گزارش اسپم ارسالی اتفاقی بوده، یا این که تمایل داشتید که دوباره ایمیل‌ها را دریافت کنید، می‌توانید دوباره با کلیک برروی گزینه دریافت اشتراک در برگه قبلی ایمیل‌ها را دریافت کنید.",
